.Ymacs-Theme-material { --ymacs-cursor-bg: #ff9800; --ymacs-cursor-fg: #263238; }
.Ymacs-Theme-material { color: #ffffff; background-color: #263238; }
.Ymacs-Theme-material .Ymacs_SplitCont .bar { background-color: #969896; }
.Ymacs-Theme-material .Ymacs_SplitCont.horiz .bar { border-top: 1px solid #cccccc; border-bottom: 1px solid #666666; }
.Ymacs-Theme-material .Ymacs_SplitCont.vert .bar { border-left: 1px solid #cccccc; border-right: 1px solid #666666; }
.Ymacs-Theme-material .Ymacs_Overlay.selection div { background-color: #555555; }
.Ymacs-Theme-material .Ymacs_Overlay.isearch div { color: #ffffff; background-color: #8bc34a; }
.Ymacs-Theme-material .Ymacs_Overlay.isearch-lazy div { color: #263238; background-color: #8bc34a; }
.Ymacs-Theme-material .Ymacs_Overlay.match-paren div { color: #000000; background-color: #32cd32; }
.Ymacs-Theme-material .Ymacs_Modeline { color: #a7adba; background-color: #1c1f26; }
.Ymacs-Theme-material .Ymacs_Frame-active .Ymacs_Modeline { color: #ffffff; background-color: #1c1f26; }
.Ymacs-Theme-material .minibuffer-prompt { color: #4dd0e1; }
.Ymacs-Theme-material .mode-line-buffer-id { color: #ffffff; font-weight: bold; }
.Ymacs-Theme-material .type { color: #84ffff; }
.Ymacs-Theme-material .builtin { color: #ff8a65; }
.Ymacs-Theme-material .function-name { color: #84ffff; }
.Ymacs-Theme-material .variable-name { color: #ffcc80; }
.Ymacs-Theme-material .constant { color: #8bc34a; }
.Ymacs-Theme-material .string { color: #9ccc65; }
.Ymacs-Theme-material .string-starter { color: #9ccc65; }
.Ymacs-Theme-material .string-stopper { color: #9ccc65; }
.Ymacs-Theme-material .regexp { color: #9ccc65; }
.Ymacs-Theme-material .regexp-starter { color: #9ccc65; }
.Ymacs-Theme-material .regexp-stopper { color: #9ccc65; }
.Ymacs-Theme-material .regexp-modifier { color: #9ccc65; }
.Ymacs-Theme-material .keyword { color: #fff59d; }
.Ymacs-Theme-material .comment { color: #b0bec5; }
.Ymacs-Theme-material .mcomment { color: #b0bec5; }
.Ymacs-Theme-material .comment-starter { color: #b0bec5; }
.Ymacs-Theme-material .mcomment-starter { color: #b0bec5; }
.Ymacs-Theme-material .mcomment-stopper { color: #b0bec5; }
.Ymacs-Theme-material .number { color: #8bc34a; }
.Ymacs-Theme-material .operator { }
.Ymacs-Theme-material .error { color: #f36c60; font-weight: bold; }
.Ymacs-Theme-material .xml-open-tag { color: #84ffff; }
.Ymacs-Theme-material .xml-close-tag { color: #84ffff; }
.Ymacs-Theme-material .xml-attribute { color: #ffcc80; }
.Ymacs-Theme-material .xml-entity-starter { color: #8bc34a; }
.Ymacs-Theme-material .xml-entity-stopper { color: #8bc34a; }
.Ymacs-Theme-material .xml-entity { color: #8bc34a; }
.Ymacs-Theme-material .xml-open-bracket { }
.Ymacs-Theme-material .xml-close-bracket { }
.Ymacs-Theme-material .xml-closetag-slash { }
.Ymacs-Theme-material .xml-cdata { color: #b0bec5; }
.Ymacs-Theme-material .xml-cdata-starter { color: #b0bec5; }
.Ymacs-Theme-material .xml-cdata-stopper { color: #b0bec5; }
.Ymacs-Theme-material .lisp-keyword { color: #ff8a65; }
.Ymacs-Theme-material .markdown-heading1 { font-size: 1.692em; }
.Ymacs-Theme-material .markdown-heading2 { font-size: 1.442em; }
.Ymacs-Theme-material .markdown-heading3 { font-size: 1.192em; }
.Ymacs-Theme-material .markdown-heading4 { font-weight: bold; }
.Ymacs-Theme-material .markdown-heading5 { font-weight: bold; font-style: italic; }
.Ymacs-Theme-material .markdown-heading6 { font-weight: bold; }
.Ymacs-Theme-material .markdown-blockquote { color: #b0bec5; }
.Ymacs-Theme-material .markdown-blockquote1 { color: #b0bec5; }
.Ymacs-Theme-material .markdown-blockquote2 { color: #b0bec5; }
.Ymacs-Theme-material .markdown-blockquote3 { color: #b0bec5; }
